.score-container {
  position: relative;
  background: #b0a091;
  padding: 0.5em;
  font-size: 1.5em;
  border-radius: 3px;
  color: #f9f6f2;
  text-align: center; }

.score-name {
  text-transform: uppercase;
  font-size: 13px;
  line-height: 1.2;
  text-align: center;
  color: #eee4da; }

.score-value {
  text-align: center;
  line-height: 1;
  font-weight: bold; }

@-webkit-keyframes move-up {
  0% {
    top: 25px;
    opacity: 1; }
  100% {
    top: -50px;
    opacity: 0; } }

@keyframes move-up {
  0% {
    top: 25px;
    opacity: 1; }
  100% {
    top: -50px;
    opacity: 0; } }

.score-addition {
  position: absolute;
  right: 30px;
  font-size: 25px;
  line-height: 25px;
  font-weight: bold;
  color: rgba(119, 110, 101, 0.9);
  z-index: 1;
  -webkit-animation: move-up 600ms ease-in;
          animation: move-up 600ms ease-in;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both; }



